import React, { useState } from "react";
import Styled from "../styles/Styles";
import Colors from "../styles/Colors";
import { useSpring, animated } from "@react-spring/web";
import { useNavigate } from "react-router-dom";
import Functions from "../util/Functions";
import routes from "../routes";
import MoonTool from "../components/MoonTool";
import TextFade from "../components/TextFade";
import { motion } from "framer-motion";
import FreeCellBoard from "../components/FreeCell/components/FreeCellBoard";
import "../components/FreeCell/FreeCell.css";

const { sleep, formatMoonPhase } = Functions;

const Projects = () => {
  const navigate = useNavigate();
  const [toolProps, toolApi] = useSpring(() => ({
    from: {
      opacity: 0,
      width: "100px",
      height: "20.5px",
      borderRadius: "5px",
      backgroundColor: Colors.GRAY
    },
    to: {
      opacity: 1,
      width: "100px",
      height: "20.5px",
      borderRadius: "5px",
      backgroundColor: Colors.GRAY
    }
  }));

  const [moonTool, setMoonTool] = useState(false);

  const [freeCellProps, freeCellApi] = useSpring(() => ({
    from: {
      opacity: 0,
      width: "100px",
      height: "20.5px",
      borderRadius: "5px",
      backgroundColor: Colors.GRAY
    },
    to: {
      opacity: 1,
      width: "100px",
      height: "20.5px",
      borderRadius: "5px",
      backgroundColor: Colors.GRAY
    }
  }));

  const [freeCell, setFreeCell] = useState(false);

  const handleMoonToolClick = async () => {
    toolApi.start({
      from: {
        opacity: 1,
        width: "100px",
        height: "20.5px",
        backgroundColor: Colors.GRAY
      },
      to: {
        opacity: 1,
        width: "400px",
        height: "600px",
        backgroundColor: Colors.MIDNIGHT_BLUE
      }
    });
    setMoonTool(true);
  };
  const handleMoonToolCloseClick = async () => {
    toolApi.start({
      from: {
        opacity: 1,
        width: "400px",
        height: "600px",
        backgroundColor: Colors.MIDNIGHT_BLUE
      },
      to: {
        opacity: 1,
        width: "100px",
        height: "20.5px",
        backgroundColor: Colors.GRAY
      }
    });
    setMoonTool(false);
  };

  const handleFreeCellClick = async () => {
    freeCellApi.start({
      from: {
        opacity: 1,
        width: "100px",
        height: "20.5px",
        backgroundColor: Colors.GRAY
      },
      to: {
        opacity: 1,
        width: "1000px",
        height: "600px",
        backgroundColor: Colors.POKER_GREEN
      }
    });
    setFreeCell(true);
  };
  const handleFreeCellCloseClick = async () => {
    freeCellApi.start({
      from: {
        opacity: 1,
        width: "1000px",
        height: "600px",
        backgroundColor: Colors.POKER_GREEN
      },
      to: {
        opacity: 1,
        width: "100px",
        height: "20.5px",
        backgroundColor: Colors.GRAY
      }
    });
    setFreeCell(false);
  };

  return (
    <Styled.FlexColumn>
      {!freeCell && (
        <div style={{ marginBottom: "20px" }}>
          <motion.div whileHover={{ scale: moonTool ? 1 : 1.1 }}>
            <animated.div style={{ ...toolProps }}>
              <Styled.ClickBox
                onClick={() => {
                  moonTool ? handleMoonToolCloseClick() : handleMoonToolClick();
                }}
              >
                <TextFade>moon tool </TextFade>
              </Styled.ClickBox>
              {moonTool && <MoonTool cityOrZip={"11237"} />}
            </animated.div>
          </motion.div>
        </div>
      )}
      {/* {!moonTool && (
        <motion.div whileHover={{ scale: freeCell ? 1 : 1.1 }}>
          <animated.div style={{ ...freeCellProps }}>
            <Styled.ClickBox
              onClick={() => {
                freeCell ? handleFreeCellCloseClick() : handleFreeCellClick();
              }}
            >
              <TextFade>freecell </TextFade>
            </Styled.ClickBox>
            {freeCell && (
              <div className="App">
                <FreeCellBoard />
              </div>
            )}
          </animated.div>
        </motion.div>
      )} */}
    </Styled.FlexColumn>
  );
};

export default Projects;
