import React, { useState } from "react";
import styled from "styled-components";
import Styled from "../styles/Styles";
import { useSpring, animated } from "@react-spring/web";
import Functions from "../util/Functions";
import { useNavigate } from "react-router-dom";
import Colors from "../styles/Colors";
import routes from "../routes";
import Intro from "../pages/Intro";
import NavBar from "../components/NavBar";
import Projects from "../pages/Projects";

const { sleep } = Functions;

type Props = {
  startTab?: string;
};

const About = ({ startTab }: Props) => {
  const navigate = useNavigate();

  const [activeTab, setActiveTab] = useState(startTab || routes.intro);
  console.log(activeTab);

  const [projectsProps, projectsApi] = useSpring(() => ({
    from: { opacity: 0, x: "400%" },
    to: { opacity: 1, x: "0%" }
  }));
  const [introProps, introApi] = useSpring(() => ({
    from: { opacity: 0, x: "800%" },
    to: { opacity: 1, x: "0%" }
  }));
  const [resumeProps, resumeApi] = useSpring(() => ({
    from: { opacity: 0, x: "1200%" },
    to: { opacity: 1, x: "0%" }
  }));

  const handleClick = async (route: string) => {
    setActiveTab(route);
    projectsApi.start({
      from: { opacity: 1, x: "0px" },
      to: { opacity: 0, x: "100px" }
    });
    introApi.start({
      from: { opacity: 1, x: "0px" },
      to: { opacity: 0, x: "100px" }
    });
    resumeApi.start({
      from: { opacity: 1, x: "0px" },
      to: { opacity: 0, x: "100px" }
    });
    await sleep(350);
    navigate(route);
  };
  return (
    <Styled.Background color={Colors.RED}>
      <Styled.FlexRow
        style={{ justifyContent: "space-between", width: "100%" }}
      >
        <NavBar handleClick={handleClick} activeTab={activeTab} />
        <div style={{ marginRight: "150px" }}>
          {activeTab === routes.intro && <Intro />}
          {activeTab === routes.stuff && <Projects />}
        </div>
        <div />
      </Styled.FlexRow>
    </Styled.Background>
  );
};

export default About;
