import React from "react";
import Styled from "../styles/Styles";
import { useSpring, animated } from "@react-spring/web";
import routes from "../routes";
import pdf from "../assets/MaxwellCooperResume.pdf";
import { motion } from "framer-motion";

type Props = {
  handleClick: (tab: string) => void;
  activeTab: string;
};

const NavBar = ({ handleClick, activeTab }: Props) => {
  const [projectsProps, projectsApi] = useSpring(() => ({
    from: { opacity: 0, x: "400%" },
    to: { opacity: 1, x: "0%" }
  }));
  const [introProps, introApi] = useSpring(() => ({
    from: { opacity: 0, x: "800%" },
    to: { opacity: 1, x: "0%" }
  }));
  const [resumeProps, resumeApi] = useSpring(() => ({
    from: { opacity: 0, x: "1200%" },
    to: { opacity: 1, x: "0%" }
  }));

  return (
    <Styled.FlexColumn style={{ marginLeft: "30px" }}>
      <div style={{ marginBottom: "20px" }}>
        <motion.div whileHover={{ scale: 1.2 }}>
          <animated.div style={{ ...introProps }}>
            <Styled.ClickBox
              selected={activeTab === routes.intro}
              onClick={() => handleClick(routes.intro)}
            >
              intro
            </Styled.ClickBox>
          </animated.div>
        </motion.div>
      </div>
      <div style={{ marginBottom: "20px" }}>
        <motion.div whileHover={{ scale: 1.2 }}>
          <animated.div style={{ ...projectsProps }}>
            <Styled.ClickBox
              selected={activeTab === routes.stuff}
              onClick={() => handleClick(routes.stuff)}
            >
              stuff
            </Styled.ClickBox>
          </animated.div>
        </motion.div>
      </div>
      <div style={{ marginBottom: "20px" }}>
        <motion.div whileHover={{ scale: 1.2 }}>
          <animated.div style={{ ...resumeProps }}>
            <Styled.ClickBox
              onClick={() => {
                window.open(pdf);
              }}
            >
              resume
            </Styled.ClickBox>
          </animated.div>
        </motion.div>
      </div>
    </Styled.FlexColumn>
  );
};

export default NavBar;
