import React, { useState } from "react";
import styled from "styled-components";
import { NavigationType, useNavigate } from "react-router-dom";
import { useSpring, animated } from "@react-spring/web";
import Styled from "../styles/Styles";
import Functions from "../util/Functions";
import Colors from "../styles/Colors";
import SpinningBoxCircle from "../components/SpinningBoxCircle";
import { motion, MotionConfig } from "framer-motion";

const { sleep } = Functions;

const Home = () => {
  const navigate = useNavigate();
  const [hover, setHover] = useState(false);
  const [websiteProps, websiteApi] = useSpring(() => ({
    from: { opacity: 0, x: "-200%" },
    to: { opacity: 1, x: "0%" }
  }));
  const [ofProps, ofApi] = useSpring(() => ({
    from: { opacity: 0 },
    to: { opacity: 1 }
  }));
  const [maxProps, maxApi] = useSpring(() => ({
    from: { opacity: 0, x: "200%" },
    to: { opacity: 1, x: "0%" }
  }));

  const handleClick = async () => {
    websiteApi.start({
      from: {
        opacity: 1,
        x: "0%"
      },
      to: {
        opacity: 0,
        x: "200%"
      }
    });
    ofApi.start({
      from: {
        opacity: 1
      },
      to: {
        opacity: 0
      }
    });
    maxApi.start({
      from: {
        opacity: 1,
        x: "0%"
      },
      to: {
        opacity: 0,
        x: "-200%"
      }
    });
    await sleep(350);
    navigate("/about");
  };

  return (
    <Styled.Background color={Colors.RED}>
      <Styled.FlexColumn>
        {" "}
        <div style={{ marginRight: "15px" }}>
          <SpinningBoxCircle diamond />
        </div>
      </Styled.FlexColumn>
      <Styled.FlexColumn>
        {" "}
        <div style={{ marginRight: "25px" }}>
          <SpinningBoxCircle />
        </div>
      </Styled.FlexColumn>
      <Styled.FlexColumn>
        <div style={{ marginBottom: "15px" }}>
          <SpinningBoxCircle diamond />
        </div>
        <div style={{ marginBottom: "15px" }}>
          <SpinningBoxCircle />
        </div>
        <div style={{ marginBottom: "20px" }}>
          <motion.div whileHover={{ scale: 1.3 }}>
            <animated.div style={{ ...websiteProps }}>
              <Styled.ClickBox
                onClick={handleClick}
                onMouseEnter={() => setHover(true)}
                onMouseLeave={() => setHover(false)}
                selected={hover}
              >
                click!
              </Styled.ClickBox>{" "}
            </animated.div>
          </motion.div>
        </div>
        <div style={{ marginBottom: "20px" }}>
          <motion.div whileHover={{ scale: 1.3 }}>
            <animated.div style={{ ...ofProps }}>
              <Styled.ClickBox
                onClick={handleClick}
                onMouseEnter={() => setHover(true)}
                onMouseLeave={() => setHover(false)}
                selected={hover}
              >
                on!
              </Styled.ClickBox>{" "}
            </animated.div>
          </motion.div>
        </div>
        <div style={{ marginBottom: "20px" }}>
          <motion.div whileHover={{ scale: 1.3 }}>
            <animated.div style={{ ...maxProps }}>
              <Styled.ClickBox
                onClick={handleClick}
                onMouseEnter={() => setHover(true)}
                onMouseLeave={() => setHover(false)}
                selected={hover}
              >
                me!
              </Styled.ClickBox>
            </animated.div>{" "}
          </motion.div>
        </div>
        <div style={{ marginBottom: "15px" }}>
          <SpinningBoxCircle />
        </div>
        <SpinningBoxCircle diamond />
      </Styled.FlexColumn>
      <Styled.FlexColumn>
        {" "}
        <div style={{ marginLeft: "25px" }}>
          <SpinningBoxCircle />
        </div>
      </Styled.FlexColumn>
      <Styled.FlexColumn>
        {" "}
        <div style={{ marginLeft: "15px" }}>
          <SpinningBoxCircle diamond />
        </div>
      </Styled.FlexColumn>
    </Styled.Background>
  );
};

export default Home;
