import React from "react";
import { motion } from "framer-motion";

type Props = {
  diamond?: boolean;
};

const SpinningBoxCircle = ({ diamond }: Props) =>
  diamond ? (
    <motion.div
      className="box"
      animate={{
        rotate: [45, 405, 405, 45],
        borderRadius: ["10%", "50%", "50%", "10%"]
      }}
      transition={{
        duration: 2,
        ease: "easeInOut",
        times: [0, 0.3, 0.6, 1],
        repeat: Infinity,
        repeatDelay: 1
      }}
    >
      <div className="diamond">{String.fromCodePoint(0x2661)}</div>
    </motion.div>
  ) : (
    <motion.div
      className="box"
      animate={{
        rotate: [0, -360, -360, 0],
        borderRadius: ["10%", "50%", "50%", "10%"]
      }}
      transition={{
        duration: 2,
        ease: "easeInOut",
        times: [0, 0.3, 0.6, 1],
        repeat: Infinity,
        repeatDelay: 1
      }}
    >
      {String.fromCodePoint(0x2661)}
    </motion.div>
  );

export default SpinningBoxCircle;
